@font-face {
    font-family: 'konfu';
    src: url('./media/fonts/konfucf.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
* {
    margin: 0;
    padding: 0;
}
.landscapeDisplay {
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
.portraitDisplay {
    position: relative;
    background-color: #fa9408;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    z-index: 0;
    overflow: hidden;
}
/*************************************************************HOME PAGE *******************************************/
.greenSide {
    background-color: #0e4d4ef8;
    height: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 40%;
    text-align: center;
    z-index: 1;
}
.coinBoxTop > img {
    position: absolute;
    height: auto;
}
.coinBoxBottom > img {
    position: absolute;
    height: auto;
    z-index: 5;
}
.logoBox {
    position: absolute;
    top: 50;
    text-align: center;
    z-index: 0;
}
.appLogo {
    width: 75%;
    height: auto;
    pointer-events: none;
}
.buttonBox {
    z-index: 6;
}
.buttonBox > a {
    text-align: center;
    display: block;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    margin-bottom: 10px;
    border-radius: 5px;
    font-family: sans-serif;
    color: white;
    text-decoration: none;
}
.title {
    text-align: center;
    font-family: "konfu", sans-serif;
    text-shadow: 
        -1px -1px 0 #000, 
        1px -1px 0 #000,  
        -1px 1px 0 #000,  
        1px 1px 0 #000; 
}
@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@media (prefers-reduced-motion: no-preference) {
    .appLogo {
        animation: App-logo-spin infinite 20s linear;
    }
}
/********************************************************GAME PAGE************************************************/
.topLine {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #0e4d4e;
	width: 100%;
	height: auto;
    padding-left: 5%;
}
.topLine > img {
    width: 64px;
    height: auto;
}
.coinBox {
    display: flex;
    justify-content: center;
}
.coinBox > img {
    width: 30%;
    height: auto;
}
.buttonBox {
    text-align: center;
    margin: 10% 5%;
}
.buttonBox > button {
    background-color: rgba(0, 0, 0, 0.5);
    outline: none;
    border: none;
    padding: 3% 10%;
    border-radius: 5px;
    color: white;
}
@keyframes shake {
    0%, 70% {
        transform: rotate(0deg); 
    }
    10%, 30%, 50%, 70%, 90% {
        transform: rotate(3deg); 
    }
    20%, 40%, 60%, 80% {
        transform: rotate(-3deg);
    }
    100% {
        transform: rotate(0deg); 
    }
}
  
.shakeAnimation {
    animation: shake 1s ease-in-out infinite; /* Применение анимации */
}
/*************************************************************RESULT******************************************************/
.resultBox {
    padding: 2%;
}
.result {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    padding: 3%;
}
.sendResult {
    text-align: center;
}
.sendResult > button {
    background-color: #ac1e22;
    outline: none;
    border: none;
    padding: 3% 10%;
    border-radius: 5px;
    color: white;
    margin-top: 5% ;
}
/****************************************************MEDIA_QUERIES************************************************************/
@media screen and (min-width: 320px) {
    .smallTextSize {
        font-size: 12px;
    }
    .mainTextSize {
        font-size: 72px;
    }
    .coinBoxTop > img {
        top: -20%;
        left: -75%;
        width: 180%;
    }
    .coinBoxBottom > img {
        right: -30%;
        bottom: -25%;
        width: 70%;
    }
    .topLine {
        display: none;
    }
    .stripBox {
        padding: 45% 25% 5% 25%;
    }
}
@media screen and (min-width: 360px) {
    .smallTextSize {
        font-size: 16px;
    }
    .mainTextSize {
        font-size: 82px;
    }
    .coinBoxTop > img {
        width: 220%;
    }
    .coinBoxBottom > img {
        width: 110%;
    }
    .topLine {
        display: block;
    }
    .stripBox {
        padding: 35% 15% 5% 15%;
    }
}
@media screen and (min-width: 600px) {
    .smallTextSize {
        font-size: 18px;
    }
    .mainTextSize {
        font-size: 112px;
    }
    .coinBoxTop > img {
        width: 180%;
    }
    .coinBoxBottom > img {
        width: 70%;
    }
    .stripBox {
        padding: 15% 15% 5% 15%;
    }
}
@media screen and (min-width: 1024px) {
    .smallTextSize {
        font-size: 22px;
    }
    .mainTextSize {
        font-size: 160px;
    }
    .coinBoxTop > img {
        width: 200%;
    }
    .coinBoxBottom > img {
        width: 80%;
    }
    .stripBox {
        padding: 15% 15% 5% 15%;
    }
}
